<template>
  <section id="i-am" class="container">
    <div class="row">
        <div class="col s12">
          <p>Je suis <span class="typed">développeur web fullstack</span></p>
        </div>
      </div>
    </section>
</template>

<script>
export default {
  name: 'i-am',
  mounted () {
    $('.typed').typed({
      strings: ['développeur web fullstack', 'en deuxième année de BTS SIO', 'développeur d\'application bureau / mobile'],
      typeSpeed: 30,
      backDelay: 5000,
      loop: true
    })
  }
}
</script>

<style>
  #i-am p {
    font-size: 32px;
    margin: 0;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    text-align: center;
    padding: 60px 0;
  }

  .typed-cursor{
    opacity: 1;
    -webkit-animation: blink 0.7s infinite;
    -moz-animation: blink 0.7s infinite;
    animation: blink 0.7s infinite;
  }
  
  @keyframes blink{
    0% { opacity:1; }
    50% { opacity:0; }
    100% { opacity:1; }
  }
  @-webkit-keyframes blink{
    0% { opacity:1; }
    50% { opacity:0; }
    100% { opacity:1; }
  }
  @-moz-keyframes blink{
    0% { opacity:1; }
    50% { opacity:0; }
    100% { opacity:1; }
  }
</style>



