<template>
  <div id="jp-card" class="col s12 m6 l4" :style="'background: url(' + img + ') no-repeat top center / cover'">
    <h5>{{ title }}</h5>
  </div>
</template>

<script>
export default {
  name: 'jp-card',
  props: {
    img: String,
    title: String
  }
}
</script>

<style>
  #jp-card {
    height: 300px;
  }

  #jp-card h5 {
    font-family: 'Raleway', sans-serif;
    font-size: 30px;
    font-weight: 300;
    color: white;
    text-align: center;
    width: 100%;
    height: 100%;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background: rgba(55,60,91,0.6);
    opacity: 0;
    transition: .5s;
  }

  #jp-card h5:hover {
    opacity: 1;
  }
  

  #jp-card.col {
    padding: 0 !important;
  }
</style>



