<template>
  <div id="home">
    <jp-navigation />
    <jp-header />
    <jp-iam />
    <jp-about-me />
    
    <section id="projects">
      <div class="row">
        <div class="col s12">
          <h4>Mes récents projets</h4>
        </div>
      </div>
      
      <div id="portfolio" class="row">
        <jp-card img="/static/img/mlo.png" title="GreenRallye" />
        <jp-card img="/static/img/powerpanel.png" title="PowerPanel" />
        <jp-card img="/static/img/parisjulien.png" title="ParisJulien.fr" />
        <jp-card img="/static/img/zenattitude.png" title="IsabelleZenAttitude" />
        <jp-card img="/static/img/florencereiki.png" title="FlorenceReiki" />
      </div>

      <div id="more-projects" class="row">
        <div class="col s12 m6 offset-m3">
          <jp-button text="Voir plus de projets" type="link" url="projects" />
        </div>
      </div>
    </section>

    <section id="blog">
      <div class="container">
        <div id="blog-text" class="row">
          <div class="col s12">
            <p>
              Venez faire un tour sur mon blog, j'y rédige des articles sur toutes sortes de choses liées
              au monde de l'informatique.
            </p>
          </div>
        </div>

        <div id="blog-button" class="row">
          <div class="col s12 m6 offset-m3">
            <jp-button text="Voir mon blog" />
          </div>
        </div>
      </div>
    </section>

    <jp-contact />
    <jp-footer />
  </div>
</template>

<script>
import JpNavigation from '@/components/JpNavigation'
import JpHeader from '@/components/JpHeader'
import JpIam from '@/components/JpIam'
import JpAboutMe from '@/components/JpAboutMe'
import JpCard from '@/components/JpCard'
import JpButton from '@/components/JpButton'
import JpContact from '@/components/JpContact'
import JpFooter from '@/components/JpFooter'

export default {
  name: 'home',
  components: {
    JpNavigation,
    JpHeader,
    JpIam,
    JpAboutMe,
    JpCard,
    JpButton,
    JpContact,
    JpFooter
  }
}
</script>

<style>
  #projects {
    background: linear-gradient(#5841B8, #8570DF);
  }

  #projects h4 {
    color: white;
    font-family: 'Raleway', sans-serif;
    font-size: 30px;
    text-align: center;
    margin: 0;
    padding: 80px 0;
  }

  #portfolio {
    padding: 0 80px 80px 80px;
  }

  #more-projects {
    text-align: center;
    padding-bottom: 80px;
  }

  #blog {
    background: #F16F4F;
    padding: 80px 0;
  }

  #blog p {
    color: white;
    font-family: 'Raleway', sans-serif;
    font-size: 22px;
    text-align: center;
  }

  @media all and (max-width: 768px) {
    #portfolio {
      padding: 0 22px 80px 22px;
    }
  }
</style>



